import React, { useState, useEffect } from "react";
import AddFuelForm from "./AddFuelForm";
import DispatchFuelForm from "./DispatchFuelForm";
import DispatchLogs from "./DispatchLogs"; // Componente para visualizar registros
import { collection, addDoc, getDocs } from "firebase/firestore";
import { db } from "../firebase";
import "../styles/FuelManager.css";

function FuelManager() {
  const [currentFuel, setCurrentFuel] = useState(0); // Inicia en 0 galones
  const [capacity] = useState(3500); // Capacidad máxima del tanque
  const [addLogs, setAddLogs] = useState([]);
  const [dispatchLogs, setDispatchLogs] = useState([]);

  // Leer los registros de Firebase al cargar la página
  useEffect(() => {
    const fetchLogs = async () => {
      const addLogsSnapshot = await getDocs(collection(db, "add_logs"));
      const dispatchLogsSnapshot = await getDocs(collection(db, "dispatch_logs"));

      // Leer los registros de adición y despacho de combustible
      const addLogsData = addLogsSnapshot.docs.map(doc => doc.data());
      const dispatchLogsData = dispatchLogsSnapshot.docs.map(doc => doc.data());

      // Calcular el nivel actual de combustible sumando los galones de los registros de adición
      let initialFuel = 0;
      addLogsData.forEach(log => {
        initialFuel += log.gallons;
      });

      // Restar los galones de los registros de despacho
      dispatchLogsData.forEach(log => {
        initialFuel -= log.gallons;
      });

      setCurrentFuel(initialFuel); // Establecer nivel de combustible inicial
      setAddLogs(addLogsData); // Guardar registros de adición
      setDispatchLogs(dispatchLogsData); // Guardar registros de despacho
    };

    fetchLogs();
  }, []); // Solo se ejecuta una vez cuando se monta el componente

  // Agregar combustible
  const addFuel = async (gallons, date, invoiceSeries, invoiceNumber) => {
    if (currentFuel + gallons > capacity) {
      alert("El tanque está lleno. No puedes agregar más combustible.");
      return;
    }
    const newFuelLevel = currentFuel + gallons;
    setCurrentFuel(newFuelLevel); // Actualizar nivel del tanque
    setAddLogs([...addLogs, { date, gallons, invoiceSeries, invoiceNumber }]); // Guardar registro local

    // Guardar en Firebase
    await addDoc(collection(db, "add_logs"), { date, gallons, invoiceSeries, invoiceNumber });
  };

  // Despachar combustible
  const dispatchFuel = async (gallons, formData) => {
    if (currentFuel - gallons < 0) {
      alert("No hay suficiente combustible para despachar esta cantidad.");
      return;
    }
    const newFuelLevel = currentFuel - gallons;
    setCurrentFuel(newFuelLevel); // Actualizar nivel del tanque
    setDispatchLogs([...dispatchLogs, { ...formData, gallons }]); // Guardar registro local

    // Guardar en Firebase
    await addDoc(collection(db, "dispatch_logs"), { ...formData, gallons });
  };

  return (
    <div className="container my-4">
      <h1 className="text-center mb-4">Gestor de Inventario de Combustible</h1>
      <div className="row">
        {/* Visualización del tanque */}
        <div className="col-md-4">
          <div className="card text-center">
            <div className="card-body">
              <h4 className="card-title">Nivel del Tanque</h4>
              <div className="tank mx-auto my-3" style={{ width: "150px", height: "300px" }}>
                <div
                  className="tank-level"
                  style={{
                    height: `${(currentFuel / capacity) * 100}%`,
                    background: "green",
                    position: "absolute",
                    bottom: 0,
                    width: "100%",
                  }}
                />
              </div>
              <p>
                {currentFuel} galones / {capacity} galones (
                {((currentFuel / capacity) * 100).toFixed(1)}%)
              </p>
            </div>
          </div>
        </div>

        {/* Formularios */}
        <div className="col-md-8">
          <div className="row">
            <div className="col-md-6 mb-3">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Agregar Combustible</h5>
                  <AddFuelForm onAddFuel={addFuel} />
                </div>
              </div>
            </div>
            <div className="col-md-6 mb-3">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">Despachar Combustible</h5>
                  <DispatchFuelForm onDispatchFuel={dispatchFuel} />
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              <div className="card mt-3">
                <div className="card-body">
                  <h5 className="card-title">Registros de Combustible Despachado</h5>
                  <DispatchLogs dispatchLogs={dispatchLogs} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FuelManager;
