import React, { useState, useRef } from "react";
import SignatureCanvas from "react-signature-canvas";
import { collection, addDoc } from "firebase/firestore";
import { db } from "../firebase";

function DispatchFuelForm({ onDispatchFuel }) {
  const [formData, setFormData] = useState({
    piloto: "",
    unidad: "",
    placa: "",
    tipoUnidad: "",
    gallons: 0,
    fecha: "",
    ruta: "",
    kilometraje: "",
    hora: "",
    signature: "", // Campo para la firma
  });

  const signaturePadRef = useRef(null);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSignature = () => {
    if (signaturePadRef.current) {
      setFormData({ ...formData, signature: signaturePadRef.current.toDataURL() });
    }
  };

  const handleClearSignature = () => {
    if (signaturePadRef.current) {
      signaturePadRef.current.clear();
      setFormData({ ...formData, signature: "" });
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await addDoc(collection(db, "dispatch_logs"), {
        ...formData,
        gallons: parseFloat(formData.gallons),
      });
      onDispatchFuel(parseFloat(formData.gallons), formData);
      setFormData({
        piloto: "",
        unidad: "",
        placa: "",
        tipoUnidad: "",
        gallons: 0,
        fecha: "",
        ruta: "",
        kilometraje: "",
        hora: "",
        signature: "", // Resetear firma
      });
    } catch (error) {
      console.error("Error al despachar combustible:", error);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="p-3">
      <h3 className="text-center mb-4">Despacho de Combustible</h3>
      <div className="row">
        <div className="col-md-6 mb-3">
          <label htmlFor="piloto" className="form-label">Piloto</label>
          <input
            type="text"
            className="form-control"
            id="piloto"
            name="piloto"
            value={formData.piloto}
            onChange={handleChange}
            placeholder="Nombre del piloto"
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="unidad" className="form-label">Unidad</label>
          <input
            type="text"
            className="form-control"
            id="unidad"
            name="unidad"
            value={formData.unidad}
            onChange={handleChange}
            placeholder="Unidad"
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="placa" className="form-label">Placa</label>
          <input
            type="text"
            className="form-control"
            id="placa"
            name="placa"
            value={formData.placa}
            onChange={handleChange}
            placeholder="Placa"
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="tipoUnidad" className="form-label">Tipo de Unidad</label>
          <input
            type="text"
            className="form-control"
            id="tipoUnidad"
            name="tipoUnidad"
            value={formData.tipoUnidad}
            onChange={handleChange}
            placeholder="Tipo de Unidad"
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="gallons" className="form-label">Galones</label>
          <input
            type="number"
            className="form-control"
            id="gallons"
            name="gallons"
            value={formData.gallons}
            onChange={handleChange}
            placeholder="Galones"
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="fecha" className="form-label">Fecha</label>
          <input
            type="date"
            className="form-control"
            id="fecha"
            name="fecha"
            value={formData.fecha}
            onChange={handleChange}
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="ruta" className="form-label">Ruta</label>
          <input
            type="text"
            className="form-control"
            id="ruta"
            name="ruta"
            value={formData.ruta}
            onChange={handleChange}
            placeholder="Ruta"
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="kilometraje" className="form-label">Kilometraje</label>
          <input
            type="number"
            className="form-control"
            id="kilometraje"
            name="kilometraje"
            value={formData.kilometraje}
            onChange={handleChange}
            placeholder="Kilometraje"
            required
          />
        </div>
        <div className="col-md-6 mb-3">
          <label htmlFor="hora" className="form-label">Hora</label>
          <input
            type="time"
            className="form-control"
            id="hora"
            name="hora"
            value={formData.hora}
            onChange={handleChange}
            required
          />
        </div>

        {/* Campo de firma */}
        <div className="col-md-12 mb-3">
          <label htmlFor="signature" className="form-label">Firma</label>
          <SignatureCanvas
            ref={signaturePadRef}
            penColor="black"
            canvasProps={{ width: 400, height: 200, className: "signature-canvas" }}
          />
        </div>
        <div className="col-md-12 mb-3 text-center">
          <button type="button" onClick={handleClearSignature} className="btn btn-secondary">Limpiar Firma</button>
        </div>
      </div>
      <div className="text-center">
        <button type="submit" className="btn btn-danger">Despachar</button>
      </div>
    </form>
  );
}

export default DispatchFuelForm;
