import React, { useState, useEffect } from "react";
import { collection, addDoc, getDocs } from "firebase/firestore";
import { db } from "../firebase";

function AddFuelForm({ onAddFuel }) {
  const [formData, setFormData] = useState({
    date: "",
    gallons: "",
    invoiceSeries: "",
    invoiceNumber: "",
  });

  const [fuelLogs, setFuelLogs] = useState([]); // Estado para los registros de combustible

  // Función para cargar los registros desde la colección "add_logs"
  const fetchFuelLogs = async () => {
    const addLogsSnapshot = await getDocs(collection(db, "add_logs"));
    const addLogsData = addLogsSnapshot.docs.map(doc => doc.data());
    setFuelLogs(addLogsData);
  };

  useEffect(() => {
    fetchFuelLogs(); // Cargar registros cuando el componente se monta
  }, []); // Este hook solo se ejecutará al cargar el componente

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const newLog = {
      ...formData,
      gallons: parseFloat(formData.gallons),
    };
    try {
      // Guardar el nuevo registro en la colección "add_logs"
      await addDoc(collection(db, "add_logs"), newLog);
      onAddFuel(parseFloat(formData.gallons), formData.date, formData.invoiceSeries, formData.invoiceNumber);
      
      // Actualizar los registros después de agregar un nuevo combustible
      fetchFuelLogs();

      // Limpiar el formulario
      setFormData({
        date: "",
        gallons: "",
        invoiceSeries: "",
        invoiceNumber: "",
      });
    } catch (error) {
      console.error("Error al agregar combustible:", error);
    }
  };

  return (
    <div>
      <form onSubmit={handleSubmit} className="p-3">
        <h3 className="text-center mb-4">Agregar Combustible</h3>
        <div className="row">
          <div className="col-md-6 mb-3">
            <label htmlFor="date" className="form-label">Fecha</label>
            <input
              type="date"
              className="form-control"
              id="date"
              name="date"
              value={formData.date}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-6 mb-3">
            <label htmlFor="gallons" className="form-label">Galones</label>
            <input
              type="number"
              className="form-control"
              id="gallons"
              name="gallons"
              value={formData.gallons}
              onChange={handleChange}
              placeholder="Galones"
              required
            />
          </div>
          <div className="col-md-6 mb-3">
            <label htmlFor="invoiceSeries" className="form-label">Serie de Factura</label>
            <input
              type="text"
              className="form-control"
              id="invoiceSeries"
              name="invoiceSeries"
              value={formData.invoiceSeries}
              onChange={handleChange}
              placeholder="Serie de Factura"
              required
            />
          </div>
          <div className="col-md-6 mb-3">
            <label htmlFor="invoiceNumber" className="form-label">Número de Factura</label>
            <input
              type="text"
              className="form-control"
              id="invoiceNumber"
              name="invoiceNumber"
              value={formData.invoiceNumber}
              onChange={handleChange}
              placeholder="Número de Factura"
              required
            />
          </div>
        </div>
        <div className="text-center">
          <button type="submit" className="btn btn-primary">Agregar</button>
        </div>
      </form>

      {/* Registros de Combustible */}
      <div className="mt-4">
        <h4>Registros de Combustible Agregado</h4>
        <div className="table-responsive">
          <table className="table table-bordered table-striped">
            <thead>
              <tr>
                <th>Fecha</th>
                <th>Galones</th>
                <th>Serie Factura</th>
                <th>No. Factura</th>
              </tr>
            </thead>
            <tbody>
              {fuelLogs.length > 0 ? (
                fuelLogs.map((log, index) => (
                  <tr key={index}>
                    <td>{log.date}</td>
                    <td>{log.gallons}</td>
                    <td>{log.invoiceSeries}</td>
                    <td>{log.invoiceNumber}</td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="4" className="text-center">
                    No hay registros.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default AddFuelForm;
