import React from "react";

function DispatchLogs({ dispatchLogs }) {
  return (
    <div className="table-responsive">
      <table className="table table-bordered table-striped">
        <thead>
          <tr>
            <th>Piloto</th>
            <th>Unidad</th>
            <th>Placa</th>
            <th>Tipo Unidad</th>
            <th>Galones</th>
            <th>Fecha</th>
            <th>Ruta</th>
            <th>Kilometraje</th>
            <th>Hora</th>
            <th>Firma</th> {/* Nueva columna para la firma */}
          </tr>
        </thead>
        <tbody>
          {dispatchLogs.length > 0 ? (
            dispatchLogs.map((log, index) => (
              <tr key={index}>
                <td>{log.piloto}</td>
                <td>{log.unidad}</td>
                <td>{log.placa}</td>
                <td>{log.tipoUnidad}</td>
                <td>{log.gallons}</td>
                <td>{log.fecha}</td>
                <td>{log.ruta}</td>
                <td>{log.kilometraje}</td>
                <td>{log.hora}</td>
                <td>
                  {/* Mostrar la firma si existe */}
                  {log.signature ? (
                    <img
                      src={log.signature}
                      alt="Firma"
                      style={{ width: "100px", height: "auto", border: "1px solid #ccc" }}
                    />
                  ) : (
                    "No firmada"
                  )}
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="10" className="text-center">
                No hay registros de despacho.
              </td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
}

export default DispatchLogs;
